function duration(dur) {
	let x = moment.duration(dur);
	let reply = x.days() == 0 ? "" : `${x.days()}d `;
	reply += `${x.hours()}h ${x.minutes()}m`;
	return reply;
}

function instance($$self, $$props, $$invalidate) {
	let $showSpinner;
	let $plexPlaylists;
	let $currPlaylist;
	component_subscribe($$self, showSpinner, $$value => $$invalidate(16, $showSpinner = $$value));
	component_subscribe($$self, plexPlaylists, $$value => $$invalidate(17, $plexPlaylists = $$value));
	component_subscribe($$self, currPlaylist, $$value => $$invalidate(3, $currPlaylist = $$value));
	const dispatch = createEventDispatcher();
	let Plex = document.plex;
	let { playlist } = $$props;

	async function loadItems() {
		set_store_value(showSpinner, $showSpinner = true);
		let data = await Plex.Playlist.getItems(playlist.ratingKey);
		$$invalidate(2, entries = data.Metadata || []);
		set_store_value(showSpinner, $showSpinner = false);
	}

	

	async function removeItem(id) {
		set_store_value(showSpinner, $showSpinner = true);
		let data = await Plex.Playlist.removeItem(playlist.ratingKey, id);
		updatePlaylists(data.Metadata[0]);
		set_store_value(showSpinner, $showSpinner = false);
	}

	

	async function moveBeforeItem(itemId, targetId) {
		set_store_value(showSpinner, $showSpinner = true);
		let id = playlist.ratingKey;
		let data = await Plex.Playlist.moveItem(id, itemId, targetId, "before");
		updatePlaylists(data.Metadata[0]);
		set_store_value(showSpinner, $showSpinner = false);
	}

	

	async function moveAfterItem(itemId, targetId) {
		set_store_value(showSpinner, $showSpinner = true);
		let id = playlist.ratingKey;
		let data = await Plex.Playlist.moveItem(id, itemId, targetId, "after");
		updatePlaylists(data.Metadata[0]);
		set_store_value(showSpinner, $showSpinner = false);
	}

	

	async function moveToTop(itemId) {
		await moveBeforeItem(itemId, entries[0].playlistItemID);
	}

	

	async function moveToBottom(itemId) {
		await moveAfterItem(itemId, entries[entries.length - 1].playlistItemID);
	}

	

	function updatePlaylists(list) {
		for (let i = 0; i < $plexPlaylists.length; i++) {
			if ($plexPlaylists[i].ratingKey === list.ratingKey) {
				set_store_value(plexPlaylists, $plexPlaylists[i] = list, $plexPlaylists);
			}
		}

		set_store_value(plexPlaylists, $plexPlaylists = [...$plexPlaylists]);
		set_store_value(currPlaylist, $currPlaylist = list);
		$$invalidate(0, playlist = list);
	}

	

	async function deletePlaylist() {
		await Plex.Playlist.destroy(playlist.ratingKey);
		set_store_value(currPlaylist, $currPlaylist = null);
		dispatch("loadPlaylists");
	}

	

	async function updatePlaylist() {
		let title = document.getElementById("playlist[title]").value;
		let summary = document.getElementById("playlist[summary]").value;
		await Plex.Playlist.update(playlist.ratingKey, { title, summary });
		dispatch("loadPlaylists");
		$$invalidate(1, showEdit = false);
	}

	

	onMount(() => {
		let destIndex = null;

		interact(".drag-handle").draggable({
			hold: 0,
			lockAxis: "y",
			modifiers: [interact.modifiers.restrict({ restriction: "ul" })],
			listeners: {
				start(event) {
					let el = event.target.parentNode;
					el.classList.add("dragged");
					el.style.zIndex = 1000;
				},
				move(event) {
					let el = event.target.parentNode; // the TR
					let x = (parseFloat(el.getAttribute("data-x")) || 0) + event.dx;
					let y = (parseFloat(el.getAttribute("data-y")) || 0) + event.dy;
					el.style.transform = `translate(${x}px, ${y}px)`;
					el.setAttribute("data-x", x);
					el.setAttribute("data-y", y);
					let currY = parseInt(el.offsetTop + y);
					let entries = Array.from(el.parentNode.childNodes).flatMap(e => e.classList ? [e] : []);

					destIndex = entries.findIndex(e => {
						let top = parseInt(e.offsetTop);
						let bot = parseInt(e.offsetTop + e.offsetHeight);

						if (currY >= top && currY <= bot) {
							return true;
						}
					});

					console.log("DestIndex", destIndex);
					entries.forEach(e => e.classList.remove("active"));
					el.parentNode.classList.remove("movetotop");

					if (destIndex >= 0) {
						entries[destIndex].classList.add("active");
					} else {
						el.parentNode.classList.add("movetotop");
					}
				},
				end(event) {
					let el = event.target.parentNode;
					let ul = el.parentNode;
					console.log("found ", destIndex);

					if (destIndex != null) {
						if (destIndex == -1) {
							moveToTop(el.dataset.id);
						} else {
							moveAfterItem(el.dataset.id, entries[destIndex].playlistItemID);
						}
					}

					let els = Array.from(el.parentNode.childNodes).flatMap(e => e.classList ? [e] : []);
					els.forEach(e => e.classList.remove("active"));
					el.parentNode.classList.remove("movetotop");
					el.removeAttribute("data-y");
					el.removeAttribute("data-x");
					el.removeAttribute("style");
					el.classList.remove("dragged");
					el.style.zIndex = null;
				}
			}
		});
	});

	const click_handler = () => set_store_value(currPlaylist, $currPlaylist = null);
	const click_handler_1 = () => $$invalidate(1, showEdit = false);
	const click_handler_2 = () => $$invalidate(1, showEdit = true);
	const click_handler_3 = movie => moveToTop(movie.playlistItemID);
	const click_handler_4 = movie => moveToBottom(movie.playlistItemID);
	const click_handler_5 = movie => removeItem(movie.playlistItemID);

	$$self.$set = $$props => {
		if ("playlist" in $$props) $$invalidate(0, playlist = $$props.playlist);
	};

	let showEdit;
	let entries;

	$$self.$$.update = () => {
		if ($$self.$$.dirty & /*playlist*/ 1) {
			$: {
				if (playlist != null) {
					loadItems();
				}
			}
		}
	};

	$: $$invalidate(1, showEdit = false);
	$: $$invalidate(2, entries = []);

	return [
		playlist,
		showEdit,
		entries,
		$currPlaylist,
		Plex,
		removeItem,
		moveToTop,
		moveToBottom,
		deletePlaylist,
		updatePlaylist,
		click_handler,
		click_handler_1,
		click_handler_2,
		click_handler_3,
		click_handler_4,
		click_handler_5
	];
}

